.statisticsContainer {
  max-width: 40%;
  margin: 5vh auto;
  display: flex;
  flex-direction: column;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  border-radius: 1.5%;
}

.title {
  margin: 25px;
  padding: 10px;
  text-transform: uppercase;
  text-align: center;
  overflow-wrap: break-word;
  color: rgba(99, 98, 98, 0.885);
  font-weight: bold;
  font-size: 2rem;
}

.statList {
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  gap: 0.01px;
}

.statList > .statListItem,
.statList > .statListItemNoTitle {
  display: flex;
  flex-direction: column;
  text-align: center;
  text-shadow: 0 0 3px #000;
  width: calc((100% - 0.04px) / 5);
  overflow-wrap: break-word;
  border: 1px solid rgba(85, 87, 87, 0.475);
}

.statListItem:first-child {
  border-radius: 0 0 0 3%;
}

.statListItem:last-child {
  border-radius: 0 0 0 3%;
}

.statListItemNoTitle:first-child {
  border-radius: 5% 0 0 5%;
}

.statListItemNoTitle:last-child {
  border-radius: 0 5% 5% 0;
}

.label {
  padding-top: 10px;
  font-size: 1rem;
  color: #fff;
  overflow-wrap: break-word;
}

.percentage {
  padding: 5px;
  font-size: 1rem;
  font-weight: bold;
  color: #fff;
}
