.friendList {
  margin: 0;
  padding: 0;
  list-style: none;
}

.item {
  width: 50%;
  display: grid;
  grid-template-columns: 1fr 2fr 3fr;
  margin: 5vh auto;
  align-items: center;
  box-shadow: rgba(90, 87, 87, 0.35) 0px 5px 20px;
  border-radius: 0.5rem;
}

.status {
  border-radius: 50%;
  margin: auto 1.7rem;
  height: 15px;
  width: 15px;
}

.online {
  background-color: green;
  composes: status;
}

.offline {
  composes: status;
  background-color: red;
}

.avatar {
  margin: 0 auto;
}

.name {
  display: flex;
  flex-direction: column;
  align-items: left;
  padding: 0;
  text-align: left;
  font-weight: bold;
  font-size: 2.5rem;
}
