.transactionHistoryTable {
  margin: 5vh auto;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  border-radius: 2%;
}

.headersRow {
  font-size: 24px;
  text-align: center;
}

.columnHeader {
  width: 30%;
  padding: 10px 30px;
  background-color: #02bbd7;
  color: #fff;
  font-size: 24px;
  text-transform: uppercase;
  text-align: center;
}

.columnHeader:first-child {
  border-radius: 5% 0 0 0;
}

.columnHeader:last-child {
  border-radius: 0 5% 0 0;
}

.valuesRow:last-child .transactionData:first-child {
  border-radius: 0 0 0 5%;
}

.valuesRow:last-child .transactionData:last-child {
  border-radius: 0 0 5% 0;
}

.valuesRow {
  width: 33%;
  color: #8a8c8e;
  font-size: 14px;
  text-align: center;
  text-transform: capitalize;
}

.valuesRow:nth-child(even) {
  background-color: #c6d4d6;
}
